<template>
  <div>
    <v-sheet tile height="54" class="d-flex">
      <v-btn icon class="ma-2" @click="$refs.calendar.prev()">
        <v-icon>mdi-chevron-left</v-icon>
      </v-btn>
      <v-select
        v-model="type"
        :items="types"
        dense
        outlined
        hide-details
        class="ma-2"
        label="type"
      ></v-select>
      <v-spacer></v-spacer>
      <v-btn icon class="ma-2" @click="$refs.calendar.next()">
        <v-icon>mdi-chevron-right</v-icon>
      </v-btn>
    </v-sheet>
    <v-sheet height="600">
      <v-calendar
        ref="calendar"
        v-model="value"
        :type="type"
        :weekdays="[1, 2, 3, 4, 5, 6, 0]"
        :events="events"
        @change="loadEvents"
      ></v-calendar>
    </v-sheet>
  </div>
</template>

<script>
  export default {
    data: () => ({
      type: "week",
      types: ["month", "week", "day"],
      events: [],
      value: null,
    }),
    props: {
      calendar: Object,
      api: Object,
    },
    mounted() {},
    methods: {
      loadEvents({ start, end }) {
        this.api.client.calendar.events
          .list({
            calendarId: this.calendar.id,
            timeMin: this.$moment(start.date).format("YYYY-MM-DDTHH:mm:ss.SZ"),
            timeMax: this.$moment(end.date)
              .add(1, "days")
              .format("YYYY-MM-DDTHH:mm:ss.SZ"),
            showDeleted: false,
            singleEvents: true,
            orderBy: "startTime",
          })
          .then((response) => {
            this.events = [];
            for (let index = 0; index < response.result.items.length; index++) {
              const element = response.result.items[index];
              this.events.push({
                id: element.id,
                name: element.summary,
                start: this.$moment(element.start.dateTime).format(
                  "YYYY-MM-DD HH:mm"
                ),
                end: this.$moment(element.end.dateTime).format(
                  "YYYY-MM-DD HH:mm"
                ),
                color: "orange",
                timed: false,
              });
            }
          });
      },
    },
    watch: {
      calendar() {
        // Hack
        this.$refs.calendar.next();
        this.$refs.calendar.prev();
      },
    },
  };
</script>

