<template>
  <v-container>
    <v-row>
      <v-col align="center">
        <v-card class="pa-12">
          <v-btn v-if="!authorized" @click="handleAuthClick"
            >Frontend Login</v-btn
          >
          <v-btn v-if="authorized" @click="handleSignOutClick">Sign Out</v-btn>
        </v-card>
      </v-col>
      <v-col>
        <v-card>
          <v-card-text>
            <v-text-field
              placeholder="AccessToken"
              v-model="accessToken"
            ></v-text-field>
            <v-btn @click="testAccessTokenLogin">Test Access Token Login</v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-select
      label="Izberite koledar"
      v-model="calendar"
      :items="calendars"
      outlined
      item-value="id"
      item-text="summary"
      return-object
      class="ma-2"
    ></v-select>
    <Calendar v-if="calendar" :calendar="calendar" :api="api"></Calendar>
  </v-container>
</template>
<script>
  import Calendar from "@/components/Home/Calendar";

  const CLIENT_ID =
    "592377715438-0asgvrvbvet6q2jlokfmvu0o2kntkri5.apps.googleusercontent.com";
  const API_KEY = "AIzaSyDg-UNp6nFAxQ8vSo4VtaG6kjTe_w9ixYQ";
  const DISCOVERY_DOCS = [
    "https://www.googleapis.com/discovery/v1/apis/calendar/v3/rest",
  ];
  const SCOPES = "https://www.googleapis.com/auth/calendar.readonly";

  export default {
    data: () => ({
      authorized: false,
      api: null,
      calendars: [],
      calendar: null,
      accessToken: null,
    }),
    created() {
      this.api = window.gapi;
      this.handleClientLoad();
    },
    methods: {
      handleClientLoad() {
        this.api.load("client:auth2", this.initClient);
      },
      initClient() {
        this.api.client
          .init({
            apiKey: API_KEY,
            clientId: CLIENT_ID,
            discoveryDocs: DISCOVERY_DOCS,
            scope: SCOPES,
          })
          .then(() => {
            this.authorized = this.api.auth2.getAuthInstance().isSignedIn.get();
            if (this.authorized) {
              this.loadCalendars();
            }
          });
      },
      testAccessTokenLogin() {
        this.api.client.setToken({ access_token: this.accessToken });
        this.loadCalendars();
      },
      handleAuthClick() {
        Promise.resolve(this.api.auth2.getAuthInstance().signIn()).then(() => {
          this.authorized = true;
          this.loadCalendars();
        });
      },
      handleSignOutClick() {
        Promise.resolve(this.api.auth2.getAuthInstance().signOut()).then(() => {
          this.authorized = false;
        });
      },
      loadCalendars() {
        this.api.client.calendar.calendarList.list().then((response) => {
          this.calendars = response.result.items;
          if (this.calendars.length === 0) {
            return;
          }
          this.calendar = this.calendars[0];
        });
      },
    },
    components: {
      Calendar,
    },
  };
</script>

